
body {
  background-color: black;
  margin: 0;
}

div {
  display: inline-block
}




.head {
  text-align: left;
}


.movieBox {
  width: 250px;
  height: 100%;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  align-items: center;
  margin: 30px auto;
  left: 0;
  right: 0;
}

.link{
  width: 100%;
  height: auto;
  margin: 0 auto;
  left: 0;
  right: 0;
}
.moviePoster {
  /* border-radius: 50%; */
  width: 70%;
  margin: 0 auto;
  left: 0;
  right: 0;
  display: block;
}

.movie-box:hover {

}

.title {
  color: white;
  display: flex;
  max-width: 220px;
  height: 30px;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis; 
  white-space: nowrap;
  margin: 10px auto;
}